import React from 'react';
import Loading from "./Loading";
import { Image } from 'antd';

class Gallery extends React.Component {
    state = {
        isLoading: true,
        users: []
    };


    fetchUsers() {
        fetch(`http://www.mocky.io/v2/5ecb5c353000008f00ddd5a0`)
            .then(response => response.json())
            .then(data =>
                this.setState({
                    gallery: data,
                    isLoading: false,
                })
            )
            .catch(error => this.setState({ error, isLoading: false }));
    }

    componentDidMount() {
        this.fetchUsers();
    }
    render() {
        const { isLoading, gallery } = this.state;
        return (
            <div className="parent-gallery-div-09067e">
                {!isLoading ? (
                    gallery.map(gallery => {
                        const { id, urls } = gallery;
                        return (
                            <div className="child-img-div-1011" key={id}>
                                <Image
                                    width={200}
                                    src={urls.small}
                                />
                            </div>
                        );
                    })
                ) : (
                    <Loading />
                )}
            </div>
        );
    }

}

export default Gallery;